
html, body, #root, .x-wrapper{
  width: 100%;
  height: 100%;
  margin: 0px;
  display: contents;
}

h1 {
  margin-bottom: 1em;
}
.modal-overlay {
  height: 100vh;
  left: 0;
  position: fixed;
  width: 100vw;
  z-index: 500;
  top: 0;
  background-color: rgba(0,0,0,.35);
}
.sbutton {
border-style: none;
color: white;
cursor: pointer;
font-weight: bold;
padding: 0.9375rem;
justify-content: right;
transition-duration: .15s;
transition-timing-function: cubic-bezier(.4,0,.2,1);
background-color: transparent;
background-image: none;
}
.close-wrapper{
  display: flex;
  justify-content: right ;
}
.modal-wrapper {
display: flex;
justify-content: center;
left: 0;
outline: 0;
overflow-x: hidden;
overflow-y: auto;
position: fixed;
top: 25%;
width: 100%;
z-index: 1000;
}

.s-modal {
display: flex;
justify-content: center;
left: 0;
outline: 0;
overflow-x: hidden;
overflow-y: auto;
position: fixed;
top: 25%;
width: 100%;
z-index: 1000;
}

.gallery-wrapper {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-flow: wrap;
}

.outter-wrapper {
  padding: .5rem  2rem;
}
.dashed {
  border-style: dashed;

}


.card-wrapper {
  display: flex;  
  flex-direction: column;
  background-color: rgb(255, 255, 255);
  border: 1px solid rgb(229, 232, 235);
  border-radius: 10px;
  position: relative;
  z-index: 2;
}
.asset-anchor {
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow: hidden;
}

.asset-anchor-wrapper {
  height: 322px;
  width: 322px;
}

.asset-anchor-wrapper-inner {
  min-height: inherit;
  border-radius: inherit;
  height: 100%;
  width: 100%;
  align-items: center;
}

.asset-anchor-wrapper-inner-2 {
  align-items: center;
  display: flex;
  justify-content: center;
  max-height: 100%;
  max-width: 100%;
  overflow: hidden;
  position: relative;
  border-radius: 10px;
  
}

.img-wrapper {
  width: auto;
  height: auto;
  max-width: 100%;
  max-height: 100%;
}

.details-wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
  overflow: hidden;
  justify-content: space-between;
  cursor: pointer;
}

.details-title-wrapper {
  padding: 12px;
  justify-content: space-between;
  display: flex;
}

.detail-title-left-wrapper {
  margin-right: 12px;
  min-width: 0px;
  width: 60%;
}

.details-title-right-wrapper {
  width: 40%;
  align-items: flex-end;
  display: flex;
  flex-direction: column;
}
.details-title-left-wrapper-inner-1 {
  display: flex;
  align-items: center;
  color: rgb(112, 122, 131);
  font-weight: 500;
  font-size: 12px;
}

.details-title-left-wrapper-inner-2 {
  width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  white-space: normal;
  color: rgb(53, 56, 64);
  font-size: 12px;
  letter-spacing: 0.1px;
  font-weight: 600;
  text-align: left;
}
.details-assets-right-wrapper-inner-1 {
  flex: 1 0 0%;
  align-items: flex-end;
  flex-direction: column;
  display: flex;
}

.sell-wrapper {
  padding: .5rem
}
.span-price {
  font-weight: 400;
  font-size: 12px;
  color: rgb(112, 122, 131);
}

.price-wrapper {
  font-size: 14px;
  height: initial;
  justify-content: flex-end;
  max-width: 13ch;
  -webkit-box-align: center;
  align-items: center;
  display: flex;
  font-weight: 600;
  color: rgb(53, 56, 64);
  width: fit-content;
}


.near-symbol {
  align-items: center;
  display: flex;
  font-weight: 600;
  color: rgb(53, 56, 64);
  width: fit-content;
  max-width: 100%;
}
.near-logo {
  height: 14px;
  width: 16px;
}
.price {
  margin-left: 0.3em;
  width: 100%;
  width: 100%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.s-header {
align-items: center;
display: flex;
flex-direction: column;
padding: 1.875rem 0.9375rem 1.875rem 0.9375rem;
}

.s-title {
  margin-bottom: 0.3125rem;
}

.s-description {
color: #C1C1C1;
text-align: center;
}
.market-wrapper {
  display: flex;
  justify-content: center;
}
.market-inner-wrapper {
  
  padding-top: 1rem;
  padding-bottom: 1rem
}

.market-result-wrapper {
  display: flex;
  justify-content: center;
  width: fit-content;
}
.market-header {
  text-align: center;
  width: 100%;
}
.text-center {
  text-align: center;
}

.pb-1 {
  padding-bottom: 1rem;
}
.top-header {
  width: 100%;
  box-shadow: rgb(4 17 29 / 25%) 0px 0px 8px 0px;
  max-width: 100vw;
  height: 72px;
  top: 0px;
  position: sticky;
  z-index: 110;
  transition: top 0.5s ease 0s;
  background-color: rgb(255, 255, 255);
}


.menu {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  padding: 1rem;
}
.nav {

}
.navbar{

}

h3 {
  display: block;
  font-size: 1.17em;
  margin-block-start: 0em;
  margin-block-end: 0em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  font-weight: bold;
}

.username {
  padding: .2rem;
}


.navbar-ul{
  display: flex
}

.wrapper {
   width: auto;
   padding: 1rem;
}
.welcome-wrapper {
  display: block;
}
.welcome-text {
  font-size: 1.2rem;
  font-weight: 500;
}
.main-wrapper {
  margin: 0 auto;
  text-align: center;
}
.navbar-li{
  padding: 0rem;
}

ul {
  list-style-type: none;
  list-style: none;
  margin-block-start: 0em;
  margin-block-end: 0em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  padding-inline-start: 0px;
}

.btn {
  display: inline-block;
  background-color: #ff424d;
  border-radius: 9999px;
  color: #fff;
  font-weight: 500;
  border: 2px solid transparent;
  line-height: 1.9;
  padding: .46875rem 1rem;
  font-size: .875rem!important;
  cursor: pointer;
}


.navbar-left {
  width: auto;
}
.navbar-right {

}
.outform-wrapper {
  box-sizing: border-box;
  transition: all 300ms cubic-bezier(0.19, 1, 0.22, 1) 0s;
  padding: 0rem;
  margin: 0rem;
}

.form-wrapper {
  border-width: 1px;
  border-style: solid;
  border-color: rgb(229, 227, 221);
  border-radius: 4px;
  box-shadow: none;
  background-color: rgb(255, 255, 255);
  overflow: hidden;
  box-sizing: border-box;
  margin: 0px auto;
  max-width: 32rem;
  width: 100%;
}

.box-wrapper {
  box-sizing: border-box;
  transition: all 300ms cubic-bezier(0.19, 1, 0.22, 1) 0s;
  padding-bottom: .3rem;
  margin: 0rem;
}
.box-in-wrapper {
  position: relative;
}

.label{
  box-sizing: border-box;
  transition: all 300ms cubic-bezier(0.19, 1, 0.22, 1) 0s;
  padding: 0rem;
  margin: 0rem;
}
.form-in-wrapper {
  box-sizing: border-box;
  transition: all 300ms cubic-bezier(0.19, 1, 0.22, 1) 0s;
  padding: 1.5rem;
  margin: 0rem;
}
.input-wrapper{
  align-items: center;
  display: flex;
  margin-bottom: 0.5rem;
  position: relative;
  margin-top: 0.5rem;
}
.input-box {
  resize: none;
  min-width: 0px;
  width: 100%;
  font-weight: normal;
  appearance: none;
  background: rgb(245, 244, 242);
  border: 1px solid rgb(229, 227, 221);
  box-shadow: none;
  box-sizing: border-box;
  color: rgb(36, 30, 18);
  height: 3rem;
  outline: none;
  padding: 0.75rem 1rem;
  transition: all 300ms cubic-bezier(0.19, 1, 0.22, 1) 0s;
  border-radius: 4px;
  font-size: 1rem !important;
}
.mint-text {
  color: rgb(36, 30, 18);
  font-family: Walsheim, sans-serif;
  position: relative;
  transition: all 300ms cubic-bezier(0.19, 1, 0.22, 1) 0s;
  text-align: center;
  font-weight: 400 !important;
  margin: 1em 0px !important;
  font-size: 1.3125rem !important;
  line-height: 1.25 !important
}
.form-btn {
  align-items: center;
  backface-visibility: hidden;
  background-color: rgb(255, 66, 77);
  border-radius: 9999px;
  border: 1px solid rgb(255, 66, 77);
  box-sizing: border-box;
  cursor: pointer;
  display: flex;
  font-weight: 500;
  height: unset;
  -webkit-box-pack: center;
  justify-content: center;
  padding: 0.78125rem 1.5rem;
  position: relative;
  pointer-events: unset;
  text-align: center;
  text-decoration: none;
  text-transform: none;
  transition: all 300ms cubic-bezier(0.19, 1, 0.22, 1) 0s;
  user-select: none;
  white-space: unset;
  width: 100%;
  color: rgb(255, 255, 255);
  font-size: 1rem !important;
}
.btn:hover  {
  color: #ff424d;
  border-color: #ff424d;
  background-color: #fff;
}

.form-btn:hover {
  color: #ff424d;
  border-color: #ff424d;
  background-color: #fff;
}




.form-btn-wrapper {
  margin: 1rem 0rem
}
